<template>
<section class="brands">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto">
      <div class="w-10/12 mx-auto">
        <div class="flex flex-row align-center justify-between">
        <swiper class="mySwiper" :modules="modules" :slides-per-view="7" :autoplay="true" :delay="5000" :data-swiper-autoplay="5000" :space-between="50" :navigation="true">
          <swiper-slide v-for="items in list" :key="items">
            <div class="border border-1 border-slate-200" style="height: 90px;width: 120px">
            <img :src="`${items.img}`" alt="" class="w-full h-full rounded-sm">
            </div>
          </swiper-slide>
        </swiper>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import{Swiper,SwiperSlide} from 'swiper/vue'
import SwiperCore , {Navigation} from "swiper";
SwiperCore.use([Navigation])
import 'swiper/css'
import 'swiper/css/navigation'
import 'swiper/css/autoplay'
import{reactive} from 'vue'
export default {
  name: "SliderBrands",
  components:{Swiper,SwiperSlide},
  setup(){
    const list=reactive([
      {img:[require('/src/assets/image/br-1.jpg')]},
      {img:[require('/src/assets/image/br-2.jpg')]},
      {img:[require('/src/assets/image/br-3.jpg')]},
      {img:[require('/src/assets/image/br-4.jpg')]},
      {img:[require('/src/assets/image/br-5.jpg')]},
      {img:[require('/src/assets/image/br-6.jpg')]},
      {img:[require('/src/assets/image/br-7.jpg')]},
      {img:[require('/src/assets/image/br-8.jpg')]},
      {img:[require('/src/assets/image/br-9.jpg')]},
      {img:[require('/src/assets/image/br-10.jpg')]},
      {img:[require('/src/assets/image/br-11.jpg')]},
    ])
    return{
      list,
      // sum,
      modules:[Navigation],
    }
  }
}
</script>

<style scoped>
.swiper-button-prev{
  visibility:hidden!important
}
.swiper-button-next{
  visibility:hidden!important
}
</style>