<template>
<section class="slider-page mt-2">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto">
      <div class="w-10/12 mx-auto">
        <div class="grid grid-cols-5 grid-rows-2 gap-1">
          <div @mouseover="flag=true" @mouseleave="flag=false" class="col-span-4 row-span-2 relative">
            <Transition :duration="500" name="fade" mode="out-in">
            <div v-show="flag" class="absolute" style="top:120px;right: 20px"><font-awesome-icon @click="decrement" :icon="['fas','angle-right']" class=" text-gray-500" :style="{fontSize:size+'px'}"></font-awesome-icon></div>
          </Transition>
            <img :src="images" alt="" class="w-full h-full rounded-md">
            <Transition :duration="500" name="fade2" mode="out-in">
            <div v-show="flag" class="absolute" style="left: 30px;top: 135px"><font-awesome-icon @click="increment" :icon="['fas','angle-left']" class=" text-gray-500" :style="{fontSize:size+'px'}"></font-awesome-icon></div>
            </Transition>
          </div>

          <div class="col-span-1 row-span-1">
            <img :src="require('../../../assets/image/slider-left-1.png')" alt="" class="w-full h-full rounded-md">
          </div>
          <div class="col-span-1 row-span-1">
            <img :src="require('../../../assets/image/slider-left-2.png')" alt="" class="w-full h-full rounded-md">

          </div>
        </div>
        <div class=" mt-2 grid grid-cols-4 grid-rows-1 gap-2">
          <div @mouseover="showSide(1)" @mouseleave="count=0" class="items overflow-hidden relative">
            <div :class="{toper : count===1}" class="rounded-md absolute w-full h-24">
            </div>
            <img :src="require('../../../assets/image/janebi-1.png')" alt="" class="w-full h-24 rounded-md">
          </div>
          <div @mouseover="showSide(2)" @mouseleave="count=0"  class="items overflow-hidden relative">
            <div :class="{toper : count===2 }" class="rounded-md absolute w-full h-24">
            </div>
            <img :src="require('../../../assets/image/janebi-2.png')" alt="" class="w-full h-24 rounded-md">
          </div>
          <div @mouseover="showSide(3)" @mouseleave="count=0" class="items overflow-hidden relative">
            <div :class="{toper : count===3}"  class="rounded-md absolute w-full h-24">

            </div>
            <img :src="require('../../../assets/image/janebi-3.png')" alt="" class="w-full h-24 rounded-md">
          </div>
          <div @mouseover="showSide(4)" @mouseleave="count=0" class="items overflow-hidden relative">
            <div :class="{toper : count===4}"  class="rounded-md absolute w-full h-24">
            </div>
            <img :src="require('../../../assets/image/janebi-4.png')" alt="" class="w-full h-24 rounded-md">
          </div>
        </div>
        <div class="w-full h-auto mx-auto">
          <img :src="require('../../../assets/image/etamode.gif')" alt="" class="rounded-md w-full" style="height: 220px">
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import {reactive,ref} from 'vue'
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
export default {
  name: "SliderPage",
  components: {FontAwesomeIcon},
  setup(){
    const count=ref(0)
    const flag2=ref(false)
    const flag=ref(false)
    const size=ref(40)
    const counter=ref(0)
    const list=reactive([
      {img:[require('/src/assets/image/slide-1.png')]},
      {img:[require('/src/assets/image/slide-2.png')]},
      {img:[require('/src/assets/image/slide-3.png')]},
      {img:[require('/src/assets/image/slide-4.png')]},
    ])
    const images=ref(require('/src/assets/image/slide-1.png'))
    function increment(){
      counter.value++
      if(counter.value > list.length-1){
        counter.value=0
      }
     images.value=list[counter.value].img
    }
    function decrement(){
      counter.value--
      if(counter.value < 0){
        counter.value=list.length-1
      }
          images.value=list[counter.value].img
    }
    setInterval(()=>{
      decrement()
    },3000)

   function showSide(data){
      count.value=data
   }
    return{
     list,counter,images,size,flag,count,flag2,increment,decrement,showSide
    }
  }
}
</script>

<style scoped>
.fade-enter-active{
  transition: all ease-in-out 0.5s;
}
.fade-leave-active{
  transition: all 0.5s cubic-bezier(1,0.5,0.8,1);
}
.fade-enter-from,
.fade-leave-to{
  transform: translateX(20px);
  opacity: 0;
}
.fade2-enter-active{
  transition: all ease-in-out 0.5s;
}
.fade2-leave-active{
  transition: all 0.5s cubic-bezier(1,0.5,0.8,1);
}
.fade2-enter-from,
.fade2-leave-to{
  transform: translateX(-20px);
  opacity: 0;
}

.toper{
  transition: all ease-in-out 0.5s;
  background-color: rgba(0,0,0,0.6);
}

</style>