<template>
<section class="last-news">
  <div class="=container mx-auto p-1">
    <div class="w-full h-auto">
      <div class="w-10/12 flex flex-row justify-around mx-auto">

        <div class="w-8/12 flex flex-col space-y-1">
          <p class="mr-2 font-thin text-center text-sm text-red-500 bg-slate-200 rounded-tl-lg rounded-bl-full w-full mx-auto mt-2 p-1" style="width: 150px">اخرین مطالب</p>
          <div class="flex flex-row">
          <div v-for="value in list" :key="value" class="flex flex-row p-1">
            <div class="flex flex-col">
              <img :src="`${value.img}`" alt="" class="rounded-md" style="width: 200px;height: 170px">
              <p class="text-slate-800 font-thin text-center my-2" :style="{fontSize:size+'px'}">{{value.title}}</p>
              <div class="flex flex-row align-center justify-between">
                <div class="flex flex-row">
                  <font-awesome-icon :icon="['fas','calender']" class="text-slate-400 text-sm"></font-awesome-icon>
                  <span class="text-slate-300" :style="{fontSize:size+'px'}">{{value.date}}</span>
                </div>
                <div>
                  <font-awesome-icon :icon="['fas','eye']" class="text-slate-300 font-thin"></font-awesome-icon>
                  <span class="text-slate-300 font-thin" :style="{fontSize:size+'px'}">{{value.visit}}</span>
                </div>
              </div>
            </div>
          </div>
          </div>
        </div>
        <div class="w-2/12">
          <p class="mr-2 font-thin text-center text-sm text-red-500 bg-slate-200 rounded-br-full rounded-bl-full my-4 p-1 w-full">اخرین اخبار</p>
          <img :src="img" class="w-full" alt="" style="height: 150px">
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import{reactive,ref} from 'vue'
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
export default {
  name: "LastNews",
  components: {FontAwesomeIcon},
  setup(){
    const counter=ref(0)
    const img=ref(require('/src/assets/image/sl-1.jpg'))
    const shop=reactive([
      {img:[require('/src/assets/image/sl-1.jpg')]},
      {img:[require('/src/assets/image/sl-2.jpg')]},
      {img:[require('/src/assets/image/sl-3.jpg')]},
    ])
    function showSlider(){
      counter.value++
      if(counter.value > shop.length-1){
        counter.value=0
      }
      img.value=shop[counter.value].img
    }
    setInterval(()=>{
      showSlider()
    },2000)
    const size=ref(13)
    const list=reactive([
      {img:[require('/src/assets/image/new-1.jpg')],date:'6 ابان 1402',visit:220,title:'بهترین تبلت های بازار کدامند ؟'},
      {img:[require('/src/assets/image/new-2.jpg')],date:'6 ابان 1402',visit:210,title:'5 اسپیکر برتر بازار ایران'},
      {img:[require('/src/assets/image/new-3.jpg')],date:'6 ابان 1402',visit:200,title:'خطرناک ترین جزیره های دنیا'},
      {img:[require('/src/assets/image/new-4.jpg')],date:'6 ابان 1402',visit:210,title:'مثلث برمودا'},
    ])
    return{
     list,size,shop,img,counter,showSlider,
    }
  }
}
</script>

